import React, { useEffect, useState } from 'react';
import { Alert, Box, Button, Switch, Container, FormControlLabel,Grid, Link, MenuItem, Select, TextField, Typography } from '@mui/material';
import samples from './samples';
import { RunnerState, Runner } from './bfi';

function App() {
  const [samplei, setSamplei] = useState(2);
  const [program, setProgram] = useState('');
  const [showLayout, setShowLayout] = useState(false);
  const [apiWaiting, setApiWaiting] = useState(false);
  const [bfreErrMsg, setBfreErrMsg] = useState('');
  const [bfcode, setBfcode] = useState('');
  const [stdin, setStdin] = useState('');
  const [stdout, setStdout] = useState('');

  const putSample = (i: number) => {
    const s = samples[i];
    // console.log(s.path);
    fetch(s.path)
      .then((res) => res.text())
      .then((program) => {
        setProgram(program);
        setBfcode('');
        setStdin(s.stdin);
        setStdout('');
      });
    // fetch
    // create-react-app
    // raw-loaderasset/sourceimport
    // eject
    // '!!raw-loader!...' 
    // file-loader
  };

  useEffect(() => { putSample(samplei) }, [samplei]);

  const codegenHandler = () => {
    const url = process.env.REACT_APP_API_URL;
    if (url === undefined) {
      throw new Error('APIURL');
    }

    setBfreErrMsg('');
    setBfcode('');
    setApiWaiting(true);

    fetch(url, {
      method: 'POST',
      body: new URLSearchParams({
        'program': program,
        'show-layout': showLayout ? '1' : '0',
      }),
    })
      .then((res) => {
        if (res.ok) {
          return res.json();
        } else {
          return Promise.reject();
        }
      })
      .then((obj) => {
        const { status, stdout, stderr } = obj;
        switch (status) {
        case 'OK':
          setBfcode(stdout);
          setBfreErrMsg(stderr);
          break;
        case 'NG':
          setBfreErrMsg(stderr);
          break;
        case 'TIMEOUT':
          setBfreErrMsg('');
          break;
        case 'MAXBUF':
          setBfreErrMsg('');
          break;
        default:
          console.error('body', obj);
          return Promise.reject();
        }
      })
      .catch(() => {
        setBfreErrMsg('');
      })
      .finally(() => {
        setApiWaiting(false);
      });
  };

  const [runnerErrMsg, setRunnerErrMsg] = useState('');
  const [runningRunner, setRunningRunner] = useState<Runner | null>(null);

  const bfRunHandler = () => {
    if (runningRunner !== null) {
      return;
    }

    setStdout('');
    setRunnerErrMsg('');

    let runner: Runner;
    try {
      let outstr = '';
      runner = new Runner(bfcode, stdin, (output, state) => {
        outstr += String.fromCodePoint(...output);
        setStdout(outstr);
        switch (state) {
          case RunnerState.Finished:
          case RunnerState.Stopped:
            setRunningRunner(null);
            break;
          case RunnerState.PointerOutOfRange:
            setRunnerErrMsg('');
            setRunningRunner(null);
            break;
          case RunnerState.Running:
            // do nothing
        }
      });
    } catch (e) {
      setRunnerErrMsg(` [] `);
      return;
    }

    setRunningRunner(runner);
    runner.run();
  };
  const bfStopHandler = () => {
    if (runningRunner !== null) {
      runningRunner.stop();
    }
  };

  return (
    <Container maxWidth="lg">
      <Box marginY={2}>
        <Typography variant="h5" marginY={1}>The Bf-Reusable Language Playground</Typography>
        <Typography variant="body1">
          <Link href="https://github.com/roodni/bf-reusable">bf-reusable</Link>
          @rood_nibrainfuck
        </Typography>
      </Box>

      <Box marginY={5}>
        :
        <Select
          size="small"
          fullWidth
          value={samplei}
          onChange={(e) => {
            const i = Number(e.target.value);
            setSamplei(i);
          }}
        >
          {samples.map((sample, i) =>
            <MenuItem value={i} key={i}>
              {sample.name}
            </MenuItem>
          )}
        </Select>
      </Box>

      <Box marginY={5}>
        bf-reusable:
        <TextField
          multiline
          minRows={10}
          maxRows={25}
          fullWidth
          placeholder="bf-reusable"
          value={program}
          onChange={(e) => { setProgram(e.target.value); }}
          InputProps={{
            sx: { fontFamily: 'Monospace', fontSize: 14, wordBreak: 'break-all' },
            spellCheck: false
          }}
        />
        <Box margin={1}>
          <FormControlLabel
            control={
              <Switch checked={showLayout} onChange={(e) => { setShowLayout(e.target.checked); }} />
            }
            label=""
          />
        </Box>
        <Box>
          <Button
            variant="contained"
            size="large"
            onClick={codegenHandler}
            disabled={apiWaiting}
          >
            
          </Button>
        </Box>
        { (bfreErrMsg !== '') &&
          <Alert severity="error" sx={{ marginY: 1 }}>
            {bfreErrMsg}
          </Alert>
        }
      </Box>

      <Grid container marginY={5} spacing={1}>
        <Grid item xs={12} md={6}>
          brainfuck:
          <TextField
            multiline
            minRows={1}
            maxRows={15}
            fullWidth
            value={bfcode}
            onChange={(e) => { setBfcode(e.target.value); }}
            placeholder="brainfuck"
            InputProps={{
                sx: { fontFamily: 'Monospace', fontSize: 14, wordBreak: 'break-all' },
                spellCheck: false
            }}
          />
        </Grid>
        <Grid item xs={12} md={6}>
          :
          <TextField
            multiline
            minRows={1}
            maxRows={15}
            fullWidth
            value={stdin}
            onChange={(e) => { setStdin(e.target.value); }}
            InputProps={{
              sx: { fontFamily: 'Monospace', fontSize: 14, wordBreak: 'break-all' },
               spellCheck: false
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <Button
            onClick={bfRunHandler}
            disabled={bfcode === '' || runningRunner !== null}
            variant="contained"
            size="large"
          ></Button>
          <Button
            onClick={bfStopHandler}
            disabled={runningRunner === null}
            variant='contained'
            size="large"
            color="error"
            sx={{ marginX: 1 }}
          ></Button>
        </Grid>
        { (runnerErrMsg !== '') &&
          <Grid item xs={12}>
            <Alert severity="error">{runnerErrMsg}</Alert>
          </Grid>
        }
        <Grid item xs={12} marginY={5}>
          :
          <TextField
            multiline
            minRows={5}
            fullWidth
            value={stdout}
            InputProps={{
              sx: { fontFamily: 'Monospace', fontSize: 14, wordBreak: 'break-all' },
              spellCheck: false,
            }}
          />
        </Grid>
      </Grid>
    </Container>
  );
}

export default App;
